import React from "react";
import { HiOutlineDocumentDownload, HiMenu } from "react-icons/hi";
import Hamburguer from "./Hamburguer";
import Menu from "./Menu";
import { useState, useEffect } from "react";

function Nav() {
  const [show, setShow] = useState(false);

  const toggle = () => {
    setShow((current) => !current);
  };

  useEffect(() => {
    console.log(show);
  }, [show]);

  return (
    <div className="">
      <div className="py-4 flex px-7 justify-between items-center mt-1 md:px-16">
        <a
          className=" hover:text-purple-400 outline outline-1 rounded-full px-4 py-2"
          href="../assets/CV.pdf"
          download
        >
          Download my CV{" "}
          <HiOutlineDocumentDownload className="inline w-[20px] h-[20px] mb-1 " />
        </a>
        <Hamburguer
          className="inline w-[36px] h-[36px] mb-1"
          show={show}
          change={toggle}
        />
      </div>
      <Menu change={toggle} show={show} />
    </div>
  );
}

export default Nav;
